import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import './Menu.css';

function Menu() {
  const [testo, setTesto] = useState('');
  const [altroChecked, setAltroChecked] = useState(true); // sempre selezionato perch abbiamo solo input libero
  const navigate = useNavigate();

  const formRef = useRef(null);

  const handleSegnalaAllergia = () => {
    if (testo.trim() === '') {
      alert("Per favore, scrivi il tuo nome seguito dall'allergia o intolleranza.");
      return;
    }
    formRef.current.submit();
    setTesto('');
    alert("Allergia segnalata con successo!");
  };

  return (
    <div className="menu-container">
      <div className="menu-content">
        <h2>Allergie e Intolleranze</h2>
<p className="intro-text">
  Per garantirvi un servizio di qualit e in totale sicurezza,  molto importante per noi conoscere eventuali allergie o intolleranze alimentari. Le vostre segnalazioni rimarranno anonime e saranno trattate con la massima riservatezza, per permetterci di organizzare al meglio il men e soddisfare ogni esigenza.
</p>


        <div className="altro-wrapper active">
          <input
            type="text"
            name="testo"
            placeholder="Scrivi il tuo nome seguito dall'allergia o intolleranza"
            value={testo}
            onChange={(e) => setTesto(e.target.value)}
          />
          {testo.trim() !== '' && (
            <button
              type="button"
              className="segnala-btn"
              onClick={handleSegnalaAllergia}
            >
              Segnala allergia
            </button>
          )}
        </div>

        {/* Form nascosto per FormSubmit */}
        <form
          ref={formRef}
          action="https://formsubmit.co/mat.nini95@gmail.com"
          method="POST"
          style={{ display: 'none' }}
        >
          <input type="hidden" name="testo" value={testo} />
          <input type="hidden" name="_next" value="http://localhost:3000" />
          <input type="hidden" name="_captcha" value="false" />
        </form>

        <button className="back-home-btn" onClick={() => navigate('/')}>
          Torna alla Home
        </button>
      </div>
    </div>
  );
}

export default Menu;
