import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './ConfermaPartecipazione.css';

function ConfermaPartecipazione() {
  const [submitted, setSubmitted] = useState(false);
  const [partecipa, setPartecipa] = useState('');
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    e.target.submit(); // invia a FormSubmit
    setSubmitted(true);
    setTimeout(() => navigate('/'), 3000); // torna alla home dopo 3s
  };

  return (
    <div className="form-page">
      <div className={`form-content ${submitted ? 'confirmation' : ''}`}>
        {submitted ? (
          <h2>Grazie! La tua conferma  stata ricevuta!</h2>
        ) : (
          <>
            <h2>Conferma Partecipazione</h2>
            <p>
              Saremo felicissimi di condividere con voi questo giorno speciale!
              La vostra partecipazione render ancora pi indimenticabile la nostra festa.
            </p>
            <form
              action="https://formsubmit.co/mat.nini95@gmail.com"
              method="POST"
              onSubmit={handleSubmit}
            >
              <div className="form-field">
                <label htmlFor="nome">Nome:</label>
                <input type="text" id="nome" name="nome" required />
              </div>

              <div className="form-field">
                <label htmlFor="cognome">Cognome:</label>
                <input type="text" id="cognome" name="cognome" required />
              </div>

              <div className="form-field">
                <label htmlFor="partecipa">Parteciperai?</label>
                <select
                  id="partecipa"
                  name="partecipa"
                  required
                  value={partecipa}
                  onChange={(e) => setPartecipa(e.target.value)}
                >
                  <option value="">Seleziona un'opzione</option>
                  <option value="S">S</option>
                  <option value="No">No</option>
                </select>
              </div>

              {partecipa === 'S' && (
                <>
                  <div className="form-field">
                    <label htmlFor="numeroPersone">Numero di persone:</label>
                    <input type="number" id="numeroPersone" name="numeroPersone" min="1" required />
                  </div>

                  <div className="form-field">
                    <label htmlFor="numeroBambini">Numero di bambini:</label>
                    <input type="number" id="numeroBambini" name="numeroBambini" min="0" required />
                  </div>
                </>
              )}

              <button type="submit" className="submit-btn">Invia conferma</button>
            </form>
            <button className="home-btn" onClick={() => navigate('/')}>
              Torna alla Home
            </button>
          </>
        )}
      </div>
    </div>
  );
}

export default ConfermaPartecipazione;
