import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Home.css';

function Home() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <div className="home-container">
      <button 
        className="hamburger-btn" 
        onClick={() => setMenuOpen(!menuOpen)}
        aria-label="Toggle menu"
      >
        <span className="bar"></span>
        <span className="bar"></span>
        <span className="bar"></span>
      </button>

      <nav className={`top-menu ${menuOpen ? 'open' : ''}`}>
        <Link to="/" onClick={() => setMenuOpen(false)}>Home</Link>
        <Link to="/menu" onClick={() => setMenuOpen(false)}>Allergie e Intolleranze</Link>
        <Link to="/conferma" onClick={() => setMenuOpen(false)}>Conferma Partecipazione</Link>
        <Link to="/iban" onClick={() => setMenuOpen(false)}>Viaggio di Nozze</Link>
      </nav>
    </div>
  );
}

export default Home;
